.page {
  display: flex;
  width: 100%;
}
.page__section {
  position: relative;
  width: 100%;
  height: 780px;
}
.page__wave-container {
  bottom: 0;
  margin: 0;
  padding: 0 0 45px;
  position: absolute;
  width: 100%;
}
.page__wave-container .canvas {
  width: 100%;
  user-select: none;
  display: inline-block;
  -webkit-user-drag: none;
  z-index: 1;
  top: 0;
  left: 0;
}

.wave-container__link {
  left: -50%;
  cursor: pointer;
  display: inline-flex;
  align-items: flex-end;
  bottom: 0;
  font-family: "GT-Haptik-Bold", sans-serif;
  font-size: 12px;
  opacity: 1;
  padding-bottom: 55px;
  position: absolute;
  text-transform: uppercase;
  transition-duration: 0.2s;
  transition-property: color;
  z-index: 10;
  color: blueviolet;
}